<template>
  <div>
    <Loader v-show="!loaded" />
    <DetailsModal v-if="isClicked" :country="selectedCountry" :close="closeModal" />
    <div class="learn_notification">
      Choose Country
      <div>
        <select @change="sortByPopulation" v-model="sort">
          <option value="" selected disabled hidden>By population</option>
          <option value="all">All</option>
          <option value="5000000">More 5 m</option>
          <option value="10000000">More 10 m</option>
          <option value="50000000">More 50 m</option>
          <option value="-5000000">Less 5 m</option>
          <option value="-2000000">Less 2 m</option>
          <option value="-1000000">Less 1 m</option>
        </select>
      </div>
    </div>
    <div id="map" v-if="loaded">    
      <v-map ref="map" :minZoom="2" :options="options.map" class="fullHeight">
        <v-geojson :geojson="sortedGeojson" :options="options.geojson"></v-geojson>
      </v-map>    
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import Vue2Leaflet from 'vue2-leaflet'
import Loader from './Loader'
import DetailsModal from '../modals/DetailsModal'

export default {
  data () {
    return {
      geojson: [],
      sortedGeojson: [],  
      options: {
        map: {
          zoom: 2,
          center: [0, 0],
          maxBounds: [[90, -180], [-60, 225]]
        },
        geojson: {
          stroke: true,
          fill: true,
          strokeColor: '#000',
          color: '#000',
          fillOpacity: 1,
          onEachFeature: this.onEachFeature
        }
      },
      loaded: false,
      isClicked: false,
      selectedCountry: {},
      sort: ''
    }
  },
  created () {
    this.createGeo()
  },
  methods: {
    createGeo () {
      this.$http.get(`../json/custom2.json`)
        .then(response => {
          this.geojson = [...response.body.features]
          this.sortedGeojson = [...this.geojson]
          this.loaded = true
        }, response => {
          console.log(response)
      })
    },
    onEachFeature (feature, layer) {
      layer.setStyle({ fillColor: this.randomColor() })
      layer.on('click', () => {
        this.selectedCountry = layer.feature.properties
        this.isClicked = true
      })
    },
    randomColor () {
      return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
    },
    closeModal () {
      this.isClicked = false
    },
    sortByPopulation () {
      this.sortedGeojson = []
      if (this.sort === 'all') {
        this.sortedGeojson = [...this.geojson]
      } else if (Math.sign(this.sort) === -1) {
        for (let i = 0; i < this.geojson.length; i++) {
          if (this.geojson[i].properties.pop_est < Math.abs(this.sort)) {
            this.sortedGeojson.push(this.geojson[i])
          }
        }
      } else {
        for (let i = 0; i < this.geojson.length; i++) {
          if (this.geojson[i].properties.pop_est > +this.sort) {
            this.sortedGeojson.push(this.geojson[i])
          }
        }
      }
    }
  },
  components: {
    'v-map': Vue2Leaflet.Map,
    'v-geojson': Vue2Leaflet.GeoJSON,
    Loader,
    DetailsModal
  },
}
</script>
<style scoped>
.chosen {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);  
}
.chosen_object {
  font-size: 100px;
  color: #fff;  
}
.learn_notification {
  position: absolute;
  right: 50px;
  top: 50px;
  width: 300px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: #404040;
  background-color: #9BFFE6;
  border-bottom: 5px solid rgba(0, 0, 0, 0.9);
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
  z-index: 900;
}
@media screen and (max-width: 400px) {
  .learn_notification {
    right: 0;
    top: 0;
    width: 100%;
  }
}
select {
  width: 150px;
  margin-top: 10px;
  padding: 5px 10px;
  border: 1px solid #2c3e50;
  outline: 0;
  border-radius: 5px;
  font: 15px 'Montserrat';
  transition: all 0.3s;
}
select:focus {
  box-shadow: 0 0 5px #2c3e50;
}
</style>


